<script lang="ts">import tippy from 'tippy.js';
import { onMount } from 'svelte';
import { createEventDispatcher } from 'svelte';
;
const dispatch = createEventDispatcher();
onMount(() => {
    if (tooltip) {
        tippy(el, {
            content: tooltip,
            animation: 'scale',
            duration: 100,
            delay: 100,
        });
    }
});
let el;
export let tooltip;
export let icon;
export let style;
</script>

<svelte:options immutable={true} />

<span bind:this={el} on:click={() => dispatch('click')} {style}>
  {@html icon}
</span>
